export default {
	data() {
		return {
			pay_success_info: ""
		}
	},

	onLoad(options) {
		var order_id = options.order_id;
		this.get_pay_success_info(order_id);
	},
	methods: {
		get_pay_success_info(order_id) {
			var param = {
				order_id: order_id
			}
			var that = this;
			this.xm_config.get_pay_success_info(param)
				.then(function(res) {
					if (res.data.status == 1) {
						if (res.data.info != null) {
							that.pay_success_info = res.data.info
						}

					} else {
						uni.showToast({
							title: res.data.msg,
							icon: "none"
						})
					}
				});
		},


		OnGoMarket: function() {
			uni.switchTab({
				url: '../home-market/home-market',
			})
		},

		OnGoField: function() {
			uni.redirectTo({
				url: '../myfield/myfield?id=' + this.pay_success_info.field_id,
			})
		},
	}
}
